/* CSS VARIABLES */
:root {
  /* colors */
  --dark: rgb(19, 18, 18);
  --light: #fff;

  /* spacing */
  --spacing: 1em;
}


/* GLOBAL STYLES */
/* normalize my workspace */
html, body, #root {
  min-height: 100%;
  margin: 0;
}

body {
  font-size: 17px;
  color: var(--dark, rgb(19, 18, 18));
  font-family: 'Montserrat', Helvetica, sans-serif;
}

a {
  color: #FD0B85;
  cursor: pointer;
}
a:hover,
a:active,
a:focus {
  background-color: #FD0B85;
  color: white;
}
a.no-hover {
  background-color: inherit
}
.footer-links a {
  color: #fff;
}
.footer-links a:hover,
.footer-links a:active,
.footer-links a:focus {
  color: white;
}
.bird:hover,
.bird:active,
.bird:focus {
  background-color: transparent;
  cursor: pointer;
}

/* squash browser defualts */
h1, h2, h3, h4, p { margin: 0; }

h2 {
  font-size: 1.2em;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
}

hr {
  border-style: dashed;
  border-width: 1px;
}

/* HELPERS */
/* flex helpers */
.flex { display: -ms-flexbox; display: flex; }
.flex-col { -ms-flex-flow: column; flex-flow: column; }
.flex-row { -ms-flex-flow: row; flex-flow: row; }
.flex-wrap { -ms-flex-wrap: wrap; flex-wrap: wrap; }
/* horizontal alignments */
.justify-end { -ms-flex-pack: end; justify-content: flex-end; }
.justify-center { -ms-flex-pack: center; justify-content: center; }
.justify-around { -ms-flex-pack: distribute; justify-content: space-around; }
.justify-between { -ms-flex-pack: justify; justify-content: space-between; }
.justify-evenly { -ms-flex-pack: space-evenly; justify-content: space-evenly; }
/* vertical alignments */
.align-items-center { -ms-flex-align: center; align-items: center; }
.align-items-start { -ms-flex-align: start; align-items: flex-start; }
.align-items-end { -ms-flex-align: end; align-items: flex-end; }
.align-content-start { -ms-flex-line-pack: start; align-content: flex-start; }
.align-contnet-end { -ms-flex-line-pack: end; align-content: flex-end; }
.align-content-center { -ms-flex-line-pack: center; align-content: center; }
.align-content-space-between { -ms-flex-line-pack: justify; align-content: space-between; }

.bg-light { background-color: var(--light, #fff); }
.bold { font-weight: bold; }
.full-height { height: 100%; }
.full-width { width: 100%; }
.page-height { height: 100vh; }
.page-width { width: 100vw; }
.inline-block { display: inline-block; }

/* spacing */
.mg-0 { margin: 0; }
.mg { margin: var(--spacing, 1em); }
.mg-t { margin-top: var(--spacing, 1em); }
.mg-b { margin-bottom: var(--spacing, 1em); }
.mg-l { margin-left: var(--spacing, 1em); }
.mg-r { margin-right: var(--spacing, 1em); }

.pd-0 { padding: 0; }
.pd { padding: var(--spacing, 1em); }
.pd-t { padding-top: var(--spacing, 1em); }
.pd-b { padding-bottom: var(--spacing, 1em); }
.pd-l { padding-left: var(--spacing, 1em); }
.pd-r { padding-right: var(--spacing, 1em); }

/* SPACING MULTIPLIER CLASSES */
.mg--lg { margin:          calc( var(--spacing, 1em) * 2 ); }
.mg-t--lg { margin-top:    calc( var(--spacing, 1em) * 2 ); }
.mg-b--lg { margin-bottom: calc( var(--spacing, 1em) * 2 ); }
.mg-l--lg { margin-left:   calc( var(--spacing, 1em) * 2 ); }
.mg-r--lg { margin-right:  calc( var(--spacing, 1em) * 2 ); }

.pd--lg { padding:          calc( var(--spacing, 1em) * 2 ); }
.pd-t--lg { padding-top:    calc( var(--spacing, 1em) * 2 ); }
.pd-b--lg { padding-bottom: calc( var(--spacing, 1em) * 2 ); }
.pd-l--lg { padding-left:   calc( var(--spacing, 1em) * 2 ); }
.pd-r--lg { padding-right:  calc( var(--spacing, 1em) * 2 ); }

.mg--xl { margin:          calc( var(--spacing, 1em) * 4 ); }
.mg-t--xl { margin-top:    calc( var(--spacing, 1em) * 4 ); }
.mg-b--xl { margin-bottom: calc( var(--spacing, 1em) * 4 ); }
.mg-l--xl { margin-left:   calc( var(--spacing, 1em) * 4 ); }
.mg-r--xl { margin-right:  calc( var(--spacing, 1em) * 4 ); }

.pd--xl { padding:          calc( var(--spacing, 1em) * 4 ); }
.pd-t--xl { padding-top:    calc( var(--spacing, 1em) * 4 ); }
.pd-b--xl { padding-bottom: calc( var(--spacing, 1em) * 4 ); }
.pd-l--xl { padding-left:   calc( var(--spacing, 1em) * 4 ); }
.pd-r--xl { padding-right:  calc( var(--spacing, 1em) * 4 ); }

.mg--sm { margin:          calc( var(--spacing, 1em) * .5 ); }
.mg-t--sm { margin-top:    calc( var(--spacing, 1em) * .5 ); }
.mg-b--sm { margin-bottom: calc( var(--spacing, 1em) * .5 ); }
.mg-l--sm { margin-left:   calc( var(--spacing, 1em) * .5 ); }
.mg-r--sm { margin-right:  calc( var(--spacing, 1em) * .5 ); }

.pd--sm { padding:          calc( var(--spacing, 1em) * .5 ); }
.pd-t--sm { padding-top:    calc( var(--spacing, 1em) * .5 ); }
.pd-b--sm { padding-bottom: calc( var(--spacing, 1em) * .5 ); }
.pd-l--sm { padding-left:   calc( var(--spacing, 1em) * .5 ); }
.pd-r--sm { padding-right:  calc( var(--spacing, 1em) * .5 ); }

/* text */
.txt-right { text-align: right; }
.txt-center { text-align: center; }
.txt-left { text-align: left; }
.txt-upper { text-transform: uppercase; }


/*btns*/
.btn {
  display: block;
  text-align: center;
  padding: 10px 20px;
  margin: 10px 0;
  color: black;
  border: 1px solid black;
  cursor: pointer;
  text-decoration: none;
  -webkit-transition: all .15s ease-in-out;
  -o-transition: all .15s ease-in-out;
  transition: all .15s ease-in-out;
}
.btn:hover,
.btn:active,
.btn:focus {
  border-color: transparent;
  background-color: #FD4399;
  color: black;
  -webkit-transition: all .15s ease-in-out;
  -o-transition: all .15s ease-in-out;
  transition: all .15s ease-in-out;
}
.btn-m {
  display: inline-block;
  margin-bottom: 20px;
}

.box-shadow {
  -webkit-box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
          box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.hidden { display: none; }

/* for deveopment purposes */
.bd { border: 1px solid lime; }

.partners li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  border: 1px solid white;
  -webkit-transition: border .20s ease-in-out;
  -o-transition: border .20s ease-in-out;
  transition: border .20s ease-in-out;
}
.partners li:hover {
  cursor: pointer;
  border: 1px solid black;
}

